import { format, parseISO, formatDistanceToNow } from 'date-fns'
import { vi, enUS } from 'date-fns/locale'
import { DATE_FORMATS } from '../constants'

/**
 * Utilities cho formatting d liu
 */

/**
 * Format s tin theo nh dng VND
 */
export const formatCurrency = (amount: number, currency = 'VND'): string => {
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency,
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(amount)
}

/**
 * Format s vi du phy phn cch
 */
export const formatNumber = (num: number): string => {
  return new Intl.NumberFormat('vi-VN').format(num)
}

/**
 * Format phn trm
 */
export const formatPercentage = (value: number, decimals = 1): string => {
  return `${value.toFixed(decimals)}%`
}

/**
 * Format ngy thng
 */
export const formatDate = (
  date: string | Date | null | undefined,
  formatStr = DATE_FORMATS.DISPLAY,
  locale = 'vi'
): string => {
  if (!date) return ''
  
  try {
    const dateObj = typeof date === 'string' ? parseISO(date) : date
    const localeObj = locale === 'vi' ? vi : enUS
    
    return format(dateObj, formatStr, { locale: localeObj })
  } catch (error) {
    return ''
  }
}

/**
 * Format thi gian tng i (vd: "2 gi trc")
 */
export const formatRelativeTime = (
  date: string | Date,
  locale = 'vi'
): string => {
  const dateObj = typeof date === 'string' ? parseISO(date) : date
  const localeObj = locale === 'vi' ? vi : enUS
  
  return formatDistanceToNow(dateObj, {
    addSuffix: true,
    locale: localeObj,
  })
}

/**
 * Truncate text vi ellipsis
 */
export const truncateText = (text: string, maxLength: number): string => {
  if (text.length <= maxLength) return text
  return text.slice(0, maxLength) + '...'
}

/**
 * Format tn file
 */
export const formatFileName = (fileName: string, maxLength = 30): string => {
  if (fileName.length <= maxLength) return fileName
  
  const extension = fileName.split('.').pop()
  const nameWithoutExt = fileName.slice(0, fileName.lastIndexOf('.'))
  const truncatedName = nameWithoutExt.slice(0, maxLength - extension!.length - 4)
  
  return `${truncatedName}...${extension}`
}

/**
 * Format file size
 */
export const formatFileSize = (bytes: number): string => {
  if (bytes === 0) return '0 Bytes'
  
  const k = 1024
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
}

/**
 * Capitalize first letter
 */
export const capitalize = (str: string): string => {
  return str.charAt(0).toUpperCase() + str.slice(1)
}

/**
 * Convert camelCase to Title Case
 */
export const camelToTitle = (str: string): string => {
  return str
    .replace(/([A-Z])/g, ' $1')
    .replace(/^./, (str) => str.toUpperCase())
    .trim()
}

/**
 * Generate initials from name
 */
export const getInitials = (name: string): string => {
  return name
    .split(' ')
    .map(word => word.charAt(0).toUpperCase())
    .slice(0, 2)
    .join('')
}